<template>
  <div class="container">
    <!-- 图片背景 -->
    <img src="/gifs/test-g.gif" alt="Cityscape" class="city-image">

    <!-- 标题部分 -->
    <div class="title-section">
      <h2 @click="toggleContent">Our Firm <span class="icon">{{ isOpen ? 'X' : '↓' }}</span></h2>
      <div v-if="isOpen" class="content-module">
        <!-- 内容模块的内容 -->
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      isOpen: false, // 用于控制内容模块的展开和收起
    };
  },
  methods: {
    toggleContent() {
      this.isOpen = !this.isOpen; // 切换展开/收起状态
    }
  }
};
</script>
<style>
.container {
  position: relative;
  width: auto;
  max-width: 100%;
}

.city-image {
  width: 100%;
  height: auto;
  display: block;
}

.title-section h2 {
  position: absolute;
  bottom: 0; /* 位于图片底部 */
  left: 50%; /* 水平居中 */
  transform: translateX(-50%); /* 精确居中对齐 */
  background-color: white;
  color: #0a364a;
  padding: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.content-module {
  display: none; /* 默认隐藏 */
  /* 其余样式 */
}

.title-section h2 .icon {
  margin-left: 10px; /* 图标和文本间距 */
}

/* 当内容模块展开时 */
.container .title-section .content-module {
  display: block;
  /* 内容模块展开时的样式 */
}
</style>
